import { useState, useEffect, useCallback } from 'react';
import { db } from '../database/db';
import { Note, NoteType } from '../types';
import { v4 as uuidv4 } from 'uuid';

export const useNotes = () => {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadNotes = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      const notesData = await db.notes.orderBy('createdAt').toArray();
      setNotes(notesData);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Ошибка загрузки заметок');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadNotes();
  }, [loadNotes]);

  const createNote = async (noteData: Omit<Note, 'id' | 'createdAt' | 'updatedAt'>) => {
    const newNote: Note = {
      ...noteData,
      id: uuidv4(),
      createdAt: new Date(),
      updatedAt: new Date()
    };
    
    try {
      await db.notes.add(newNote);
      setNotes(prevNotes => [...prevNotes, newNote]);
      return newNote;
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Ошибка создания заметки');
      throw err;
    }
  };

  const updateNote = async (id: string, updates: Partial<Note>) => {
    try {
      const updateData = {
        ...updates,
        updatedAt: new Date()
      };
      
      await db.notes.update(id, updateData);
      setNotes(prevNotes => 
        prevNotes.map(note => 
          note.id === id ? { ...note, ...updateData } : note
        )
      );
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Ошибка обновления заметки');
      throw err;
    }
  };

  const deleteNote = async (id: string) => {
    try {
      await db.notes.delete(id);
      setNotes(prevNotes => prevNotes.filter(note => note.id !== id));
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Ошибка удаления заметки');
      throw err;
    }
  };

  return {
    notes,
    loading,
    error,
    createNote,
    updateNote,
    deleteNote,
    refetch: loadNotes
  };
};
